import React from 'react'
import './experienceSummary.css'

const ExperienceSummary = experience => (
  <div className="experience-summary">
    <header>
      <a
        href={experience.organization.url}
        target="_blank"
        title={`Open ${experience.organization.name}'s website in a new window`}
        rel="noopener"
      >
        <img
          className="organization-logo"
          src={experience.organization.logoUrl}
          alt={`${experience.organization.name}'s logo`}
        />
      </a>
    </header>
    <section className="main">
      <h5 className="bold">{experience.organization.name}</h5>
      <h6> {`${experience.period.from} - ${experience.period.to}`} </h6>
      <p dangerouslySetInnerHTML={{ __html: experience.summary }} />
    </section>
  </div>
)

export default ExperienceSummary
